.info__pokemon-stats {
    width: 100%;
    display: flex;
    height: 250px;
    justify-content: center;
    align-items: center;
  }
  
  .info__pokemon-stats > .pokemon-stats-left,
  .info__pokemon-stats .pokemon-stats-right {
    padding: 5px;
    height: 240px;
  }
  
  .info__pokemon-stats > .pokemon-stats-left > .pokemon-stats-title,
  .info__pokemon-stats .pokemon-stats-right > .pokemon-stats-title {
    padding: 0px 5px;
    font-size: 0.9rem;
    text-transform: uppercase;
    font-weight: 600;
    text-align: center;
    position: relative;
    color: #dfdfdf;
    display: block;
    border-bottom: 1px solid #2C5364;
  }
  
  .pokemon-stats-left {
    width: 100%;
    height: 100%;
  }
  
  .pokemon-stats-left > .pokemon-stat {
    display: flex;
    flex-direction: column;
    text-align: left;
    margin-left: 20px;
    margin-top: 0.625rem;
  }
  
  .pokemon-stats-left > .pokemon-stat > h5 {
    font-weight: 600;
    font-size: 0.75rem;
    text-transform: uppercase;
    margin-left: 5px;
    margin-bottom: 5px;
    color: #dfdfdf;
  }
  
  .pokemon-stats-left > .pokemon-stat > .progress {
    width: 100%;
    height: 0.75rem;
    background: #1c1f25;
    border-radius: 2px;
  }
  
  .pokemon-stats-left > .pokemon-stat > .progress > .progress-bar {
    height: 100%;
    width: 0%;
    background: linear-gradient(90deg, #0084ac 0%, #53d7ff 100%);
    border-radius: 2px;
    position: relative;
    transition: all 1.5s;
  }
  
  .pokemon-stats-left > .pokemon-stat > .progress > .progress-bar > div {
    position: absolute;
    right: 5%;
    color: #242424;
    font-weight: 600;
    font-size: 10px;
  }
  
  .pokemon-stats-right {
    width: 100%;
    height: 100%;
  }
.card-pokemon {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  
.card-pokemon >  .card-pokemon-number {
    padding: 10px 30px;
    text-transform: capitalize;
    font-weight: 600;
    background: #00B4DB;
    color: #dfdfdf;
    background: linear-gradient(to right, #00B4DB, #0083B0);
    font-size: 1.2rem;
    line-height: 1.2rem;
    top: 0;
    border-radius: 5px;
  }
  
  .card-pokemon >  .info__pokemon-name {
    font-size: 3rem;
    font-weight: 600;
    text-transform: uppercase;
    color: #f5f3f3;
    margin: 0;
  }
  
  .card-pokemon > .pokemon-container-img {
    width: 100%;
    height: 350px;
    display: flex;
    justify-content: center;
    position: relative;
    padding: 10px;
  }
  
  .card-pokemon >  .pokemon-container-img > .pokemon-img {
    z-index: 1;
    max-width: 350px;
    max-height: 350px;
    min-height: 300px;
    min-height: 300px;
    -webkit-filter: drop-shadow(0 0 4px #0f0c29d0);
            filter: drop-shadow(0 0 4px #0f0c29d0);
  }
  
  .card-pokemon > .info__general {
    display: flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    width: 100%;
    position: relative;
  }
  
  .card-pokemon > .info__general > .info__pokemon-type {
    width: 100%;
    display: flex;
    color: #dfdfdf;
    font-weight: 600;
    justify-content: center;
  }
  
  .card-pokemon > .info__general > .info__pokemon-type > img {
    width: 48px;
    height: 48px;
    transform: translateX(5px);
    -webkit-filter: drop-shadow(0 0 2px rgba(0, 0, 0, 0.5));
            filter: drop-shadow(0 0 2px rgba(0, 0, 0, 0.5));
  }
  
  .card-pokemon > .info__general > .info__pokemon_size {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: -5px;
    width: 100%;
    color: #dfdfdf;
  }
  
  .card-pokemon > .info__general > .info__pokemon_size > .info__pokemon-exp {
    margin-left: 20px;
    font-size: 1rem;
    font-weight: 600;
    text-transform: uppercase;
    color: #aaa9a9;
  }
  
  .card-pokemon > .info__general > .info__pokemon_size > p {
    font-size: 10px;
    text-transform: uppercase;
    font-weight: 600;
    display: flex;
    flex-direction: column-reverse;
  }
  
  .card-pokemon > .info__general > .info__pokemon_size > p > span {
    font-size: 14px;
    text-transform: lowercase;
  }
