  .container-fluid > .row > .bar-menu-inferior {
    position: fixed;
    z-index: 2;
    bottom: 0;
    width: 100%;
    height: 50px;
    background-image: linear-gradient(to bottom, rgba(36, 36, 62, 0.37), rgba(48, 43, 99, 0.349), rgba(15, 12, 41, 0.322)), url(/pokedex-react/static/media/bgBall.2cd384db.jpg);
    display: flex;
    justify-content: center;
  }
  
  .container-fluid > .row > .bar-menu-inferior > button {
    background: #0a0a0a;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    padding: 5px;
    border: none;
  }
  
  .container-fluid > .row > .bar-menu-inferior > button > img {
    width: 100%;
    height: 100%;
    transition: .3s all;
  }
  
  .container-fluid > .row > .bar-menu-inferior > button > img:hover {
    transform: scale(1.1);
  }

  .container-fluid > .row > .bar-menu-inferior > button > img:active {
    transform: scale(1.2);
  }

  
  .container-fluid > .row > .bar-menu-inferior > .input-search {
    position: absolute;
    top: 0;
    right: 0;
    margin: 5px;
  }
  
  .container-fluid > .row > .bar-menu-inferior > .input-search > input[type="text"] {
    background: transparent;
    width: 100px;
    color: #fff;
    font-weight: 600;
  }
.pokemon-moves__ability,
.pokemon-moves__moves {
  overflow: hidden;
}

.pokemon-moves__ability > .pokemon-special-move,
.pokemon-moves__moves > .pokemon-special-move {
  color: #dfdfdf;
  font-weight: 600;
  font-size: .9rem;
  margin: 5px 0;
  background: #e35d5b;
  padding: 5px;
  width: 100%;
  text-transform: capitalize;
  text-align: center;
}

.pokemon-moves__ability > .pokemon-special-move.ability,
.pokemon-moves__moves > .pokemon-special-move.ability {
  background: radial-gradient(circle, #272b35 0%, #0F2027 100%);
  text-align: center;
}

.pokemon-moves__ability {
  width: 100%;
  height: 80px;
}

.pokemon-moves__moves {
  display: flex;
  flex-direction: column;
  height: 120px;
}
* {
    padding: 0;
    margin: 0;
    font-family: 'Roboto Slab', serif;
    
  }
  
  :root {
    --widthImg: 300px;
    /* --url: '../img/bgBall.jpg'; */
    --url: '../../images/bgBall.jpg';


  }
  
  .container-fluid {
    background-image: linear-gradient(to bottom, rgba(36, 36, 62, 0.37), rgba(48, 43, 99, 0.349), rgba(15, 12, 41, 0.322)), url(/pokedex-react/static/media/bgBall.2cd384db.jpg);
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    width: 100vw;
    justify-content: center;
    height: 100vh;
    display: flex;
    transition: .3s all;
    padding: 0 0;
  }
  
  .container-fluid > .row {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
  }
  
  .container-fluid > .row > .col-left,
  .container-fluid > .row .col-middle,
  .container-fluid > .row .col-right {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  
  .container-fluid > .row > .col-left {
    position: relative;
    text-align: center;
  }
  
  .container-fluid > .row > .col-middle {
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: relative;
  }
    
  .pokemon-stats-title {
    padding: 0px 5px;
    font-size: 0.9rem;
    text-transform: uppercase;
    font-weight: 600;
    text-align: center;
    position: relative;
    color: #dfdfdf;
    border-bottom: 1px solid #2C5364;
  }
  
  @media (max-width: 576px) {
    .container-fluid {
      width: 100%;
      height: 100%;
    }
    .row {
      min-width: 100%;
    }
    .col-left {
      order: 2;
    }
    .col-middle {
      order: 1;
    }
    .col-right {
      order: 3;
              margin-bottom: 20px !important;
              margin-top: 50px !important;
              padding-bottom: 50px !important;
    }
    .info__pokemon-name {
      font-size: 2.5rem !important;
    }
    .pokemon-container-img {
      height: 325px !important;
    }
    .pokemon-img {
      max-width: 300px !important;
    }
    .button-generate {
      margin-top: 20px;
      position: relative !important;
    }
    .pokemon-stats-left,
    .pokemon-stats-right {
      min-width: 90% !important;
    }
  }
  
  @media (max-width: 380px) {
    .container-fluid {
      width: 100% !important;
      height: 100% !important;
    }
    .row {
      min-width: 100%;
    }
    .col-left {
      order: 2;
    }
    .col-middle {
      order: 1;
    }
    .col-right {
      order: 3;
      margin-bottom: 20px !important;
      margin-top: 30px;
    padding-bottom: 40px;
    }
    .pokemon-container-img {
      height: 335px !important;
    }
    .button-generate {
      position: relative !important;
    }
  }
  
  @media (max-width: 768px) {
    .button-generate {
      position: relative !important;
    }
    .pokemon-container-img {
      height: 420px !important;
    }
    .card-pokemon-number{
      margin-top: 40px !important;
    }
    .pokemon-img {
      max-width: 300px !important;
    }
    .info__pokemon-name {
      font-size: 2.5rem !important;
      margin: 10px 0 0 0 !important;
    }
  }
  
  @media (max-width: 994px) {
    .button-generate {
      position: relative !important;
    }
    .info__pokemon-name {
      font-size: 2.5rem !important;
    }
    .info__general {
      margin-bottom: 30px !important;
    }
  }
